@tailwind base;
@tailwind components;
@tailwind utilities;

html,
body{
   height: 100%;
}

.swiper .swiper-wrapper{
   width: 85vw;
   align-items: center;
   .swiper-slide{
      margin-right: 0;
   }
}

.link-toggle-burger{
   @apply text-3xl font-semibold p-1 hover:bg-[#3a4f89] active:bg-[#27355b] rounded transition duration-200;
}

.link-toggle{
   @apply text-[16px] font-semibold p-1 hover:bg-[#3a4f89] active:bg-[#27355b] rounded transition duration-200;
}

.link-main{
   @apply transition duration-200 text-[16px] font-semibold hover:text-white/80 active:text-white
}

.loader {
   width: 48px;
   height: 48px;
   border: 3px dotted #FFF;
   border-style: solid solid dotted dotted;
   border-radius: 50%;
   display: inline-block;
   position: relative;
   box-sizing: border-box;
   animation: rotation 2s linear infinite;
}
.loader::after {
   content: '';  
   box-sizing: border-box;
   position: absolute;
   left: 0;
   right: 0;
   top: 0;
   bottom: 0;
   margin: auto;
   border: 3px dotted #FF3D00;
   border-style: solid solid dotted;
   width: 24px;
   height: 24px;
   border-radius: 50%;
   animation: rotationBack 1s linear infinite;
   transform-origin: center center;
}
@keyframes rotation {
   0% {
   transform: rotate(0deg);
   }
   100% {
   transform: rotate(360deg);
   }
} 
@keyframes rotationBack {
   0% {
   transform: rotate(0deg);
   }
   100% {
   transform: rotate(-360deg);
   }
} 